.page {
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
  overflow-x: hidden;
}

.page__body {
  place-content: start center;
  min-width: 320px;
  margin: 0;
  overflow-x: hidden;
}

.page__button {
  z-index: 2;
  opacity: 1;
  pointer-events: all;
  transition: all .3s;
  position: fixed;
  bottom: 10px;
  left: 50%;
  transform: translate(-50%);
}

