    bottom: 31px;
  }
}

.page__button--hidden {
  opacity: 0;
  pointer-events: none;
}

.page__menu {
  z-index: 3;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: auto;
  transform: translateX(-100px);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.header {
  z-index: 2;
  opacity: 1;
  pointer-events: all;
  transition: all .3s;
  display: none;
  position: fixed;
  top: 0;
  left: 50%;
