.page {
  font-family: Inter, sans-serif;
  scroll-behavior: smooth;
  overflow-x: hidden;

  &__body {
    margin: 0;
    min-width: 320px;
    overflow-x: hidden;

    place-content: start center;
  }

  &__button {
    position: fixed;
    z-index: 2;
    bottom: 10px;
    left: 50%;
    transform: translate(-50%);

    transition: all 0.3s;
    opacity: 1;
    pointer-events: all;

    @include on-tablet {
      bottom: 31px;
    }

    &--hidden {
      opacity: 0;
      pointer-events: none;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 3;

    overflow: auto;

    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    transform: translateX(-100px);

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }
}
